<template>
  <div id="app">
    <h1 class="text-light p-2 p-lg-3 m-0 shadow">
      RSBP Big Garden Birdwatch
      <span> 2020 results</span>
    </h1>
    <div class="loader" :class="{ 'is-paused fade': !this.loaded }">
      <div class="lds-ripple">
        <div></div>
        <div></div>
      </div>
    </div>
    <div class="legend">
      <label for="bird" class="sr-only">Select Bird:</label>
      <dropdown-list id="bird" :options="birdOptions" v-model="selectedBird" />
      <div>
        <figure class="d-none d-sm-block mb-0">
          <img
            class="img-fluid w-100"
            :src="imageFilePath"
            :alt="selectedBird"
          />
          <figcaption class="sr-only">
            {{ selectedBird }}
          </figcaption>
        </figure>
        <h2
          v-if="selectedCounty"
          :style="{ borderLeftColor: highlightedCounty.activeColor }"
        >
          {{ highlightedCounty.county }}
        </h2>
        <p v-if="selectedCounty">
          <span>{{ highlightedCounty.percent_gardens_2020 }}% </span>
          of gardens
        </p>
      </div>
    </div>
    <google-map
      name="bird-map"
      :countyData="countyData"
      :birdData="birdData"
      :selectedBird="selectedBird"
      @highlight="highlightCounty"
      @reset="resetCounty"
      @mapLoaded="mapIsLoaded"
      @mapLoading="mapIsLoading"
    />
  </div>
</template>

<script>
import DropdownList from "./components/DropdownList.vue";
import birdData from "./assets/data/birddata.json";
import countyData from "./assets/data/counties.json";
import GoogleMap from "./components/GoogleMap";

export default {
  name: "App",
  components: {
    DropdownList,
    GoogleMap
  },
  data() {
    return {
      loaded: false,
      selectedCounty: null,
      birdData,
      countyData,
      birdOptions: [],
      selectedBird: "Blackbird"
    };
  },
  methods: {
    highlightCounty(county) {
      this.selectedCounty = county;
    },
    resetCounty() {
      this.selectedCounty = null;
    },
    mapIsLoaded() {
      this.loaded = true;
    },
    mapIsLoading() {
      this.loaded = false;
    },
    setColor(percent) {
      if (percent > 95) {
        return "#990100";
      } else if (percent > 90) {
        return "#cc0001";
      } else if (percent > 85) {
        return "#fe0000";
      } else if (percent > 80) {
        return "#ff3300";
      } else if (percent > 75) {
        return "#ff6600";
      } else if (percent > 70) {
        return "#fe9900";
      } else if (percent > 65) {
        return "#ffcc00";
      } else if (percent > 60) {
        return "#fffc00";
      } else if (percent > 55) {
        return "#cbfe33";
      } else if (percent > 50) {
        return "#98ff66";
      } else if (percent > 45) {
        return "#67ff9a";
      } else if (percent > 40) {
        return "#33ffcc";
      } else if (percent > 35) {
        return "#00ffff";
      } else if (percent > 30) {
        return "#00ccff";
      } else if (percent > 25) {
        return "#0099ff";
      } else if (percent > 20) {
        return "#0166ff";
      } else if (percent > 15) {
        return "#0000fe";
      } else if (percent > 10) {
        return "#0033ff";
      } else if (percent > 5) {
        return "#0000cc";
      } else {
        return "#000098";
      }
    }
  },
  computed: {
    imageFilePath() {
      return require("./assets/images/" +
        this.selectedBird.replace(/\s+/g, "-").toLowerCase() +
        ".png");
    },
    highlightedCounty() {
      var highlighted = this.birdData.find(item => {
        return (
          item.county.match(this.selectedCounty) &&
          item.species.match(this.selectedBird)
        );
      });
      highlighted.activeColor = this.setColor(highlighted.percent_gardens_2020);
      return highlighted;
    }
  },
  created() {
    var uniqueBirds = [];
    this.birdData.filter(bird => {
      if (uniqueBirds.indexOf(bird.species) === -1) {
        return uniqueBirds.push(bird.species);
      }
    });
    this.birdOptions = uniqueBirds.sort();
  }
};
</script>

