  height: 100%;
  margin: 0;
  opacity: 1;
  position: relative;
  width: 100%;
  z-index: 5;
}
.lds-ripple {
  display: block;
  height: 64px;
  position: absolute;
  top: calc(50% - 64px);
  left: calc(50% - 64px);
  width: 64px;
  transform: translate(50%, 50%);
  z-index: 5;
  div {
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
    border-radius: 50%;
    border: 4px solid #ccc;
